@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400,700);
html {
    height:100%;
}

body {
    font-family: 'Roboto', sans-serif;
    background: #222222;
    color: white;
    margin:0;
    min-height: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 65px;
    background: #141414;
    text-transform: uppercase;
}

.nav__left {
    display: flex;
    flex-direction: row;
    margin-left: 18%;
}

.nav__right {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 18%;
}

.nav__right > *:not(:last-child){
    margin-right: 28px;
}

.nav__right > a {
    
    font-size: 14px;
    color: #aaa;
    cursor: pointer;
}

.nav__right > a:hover {
    color: #bbb
}

.nav__button {
    font-family: 'Roboto', sans-serif !important;
    border: none;
    text-align: center;
    width: 120px;
    height: 35px;
    text-transform: capitalize;
    border-radius: 4px;
    color: #ccc;
    background: #719E2F;
    cursor: pointer;
}

.nav__button:hover {
    background: #72a526;
}

.nav__title {
    font-family: 'Roboto Condensed', sans-serif !important;
    font-weight: 300;
    font-size: 22px;
}

.nav__title:hover {
    color:#ca452f !important;
    cursor: pointer;
}

.nav__title > span {
    color:#ca452f !important;
}

.login-background-top {
    z-index: -1000;
    position:fixed;
    width:100%;
    height:45vh;
    background: #141414;
}
.container {
    display:flex;
    flex-direction: column;
    height:100vh;
    justify-content: center;
    align-items: center;
}

#login-title {
    margin:0 0 15px 0;
    text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif !important;
    font-weight: 300;
}

#login-title > span {
    color:#ca452f !important;
}

.login-panel {
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #333333;
    width:450px;
    padding:30px 0 15px 0;
}

.login-panel__error {
    color: #ca452f;
}

.login-panel__success {
    color: green;
}

.login-panel form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
}

.login-panel input[type=text],[type=password],[type=submit], [type=email] {
    width: 85%;
    outline:none;
    border:none;
    margin:10px;
    height:55px;
    font-size:1em;
    border-radius: 3px;
}

.login-panel input[type=text],[type=password],[type=email] {
    padding-left:35px;
    width: calc(85% - 35px);
}

.login-panel input[type=submit] {
    background: #ca452f;
    color:white;
    margin-bottom:20px;
}

.login-panel input[type=submit]:hover {
    cursor: pointer;
    background: #ce523d;
}

.login-panel form > span {
    margin: 7px 0 7px 0;
}

.login-panel form > span > a {
    text-decoration: none;
    color:#ca452f;
    font-size:inherit;
    cursor: pointer;
}

.footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background: #141414;
    width: 100%;
    height: 65px;
    font-size: 14px;
}

.footer__left {
    display: flex;
    flex-direction: row;
    margin-left: 18%;
}

.footer__left > p {
    font-style: italic;
    color: #888;
}

.footer__right {
    display: flex;
    flex-direction: row;
    margin-right: 18%;
    color: #aaa;
}

.footer__right > *:not(:last-child){
    margin-right: 24px;
}

.footer__right > a:hover {
    color: #bbb;
    cursor: pointer;
}
